@use 'base/var' as v;
@use 'base/mixins' as m;

*{
    font-family: 'DotGothic16', sans-serif;
}

#body{
    /* Encriptador - 1 Desktop */
    position: relative;
    width: 1440px;
    height: 1024px;

    /* Light blue/light-blue-100 */
    background: #F3F5FC;
}
.logo{
    /* Auto layout */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 40px;

    position: absolute;
    width: 120px;
    height: 48px;
    left: 40px;
    top: 40px;
}