html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  list-style-type: none;
}

body {
  font-family: "Roboto Mono", monospace;
  font-size: 1.6rem;
  background-color: white;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #050505;
  line-height: 1.5;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.center-element {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Global class flex and positions */
.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

/* Gap Class */
.g-2 {
  gap: 2rem;
}

.g-4 {
  gap: 4rem;
}

.g-6 {
  gap: 6rem;
}

/* Pasdding class */
.p-2 {
  padding: 2rem;
}

.p-4 {
  padding: 4rem;
}

.p-6 {
  padding: 6rem;
}

/* Margin class */
.m-2 {
  margin: 2rem;
}

.m-6 {
  margin: 4rem;
}

.m-6 {
  margin: 6rem;
}

.my-2 {
  margin: 2rem 0;
}

.my-4 {
  margin: 4rem 0;
}

.mx-2 {
  margin: 0 2rem;
}

.mx-4 {
  margin: 0 4rem;
}

/* Text Class */
.text-center {
  text-align: center;
}

.header {
  background-image: url("../img/_mission.png");
  background-color: rgba(120, 120, 120, 0.02);
  height: 50rem;
  background-size: cover;
  background-repeat: no-repeat;
  /* Centrar horital y ventical puedes poner % */
  background-position: center center;
}
.header a {
  font-size: 2rem;
  color: white;
}
.header .flex {
  flex-direction: column;
  margin: 0;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header .flex {
    flex-direction: row;
    margin: 0 4rem;
    justify-content: space-between;
  }
}

.box {
  border: 2px solid white;
}

/* Animacion de texto */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
.header-text {
  color: white;
  max-width: 70%;
  margin: 5rem auto;
}
@media (min-width: 480px) {
  .header-text {
    display: flex;
    flex-direction: column;
  }
}
.header-text .typing-animation .typing-text {
  overflow: hidden;
  white-space: pre-line;
  width: 0;
  animation: typing 3s steps(20, end) 1s forwards;
  color: white;
}
@media (min-width: 768px) {
  .header-text .typing-animation .typing-text {
    white-space: nowrap;
    display: inline-block;
  }
}

.cards-container {
  background-color: #e1e1e1;
  padding: 5rem 0;
}
.cards-container ul {
  height: 100%;
  width: 50%;
}
.cards-container .cards-evidence {
  width: auto;
  display: grid;
  gap: 2rem;
  height: auto;
  padding: 0;
  /* Tengo que poner los querys de menor a mayor resolucion
  en cascada. */
}
@media (min-width: 768px) {
  .cards-container .cards-evidence {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 992px) {
  .cards-container .cards-evidence {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.cards-container .cards-evidence li {
  background-color: white;
  border-radius: 1rem;
  min-width: 40rem;
  max-width: 40rem;
  margin: 0 auto;
  padding: 2rem;
}
.cards-container .cards-evidence li h4 {
  font-size: 2rem;
  font-weight: 700;
}
.cards-container .cards-list li {
  background-color: white;
  border-radius: 1rem;
  padding: 1.5rem;
  padding-bottom: 0;
  width: auto;
  max-width: 40rem;
  height: 30rem;
  margin: 0;
}
.cards-container .cards-list img {
  border-radius: 1rem;
}
.cards-container .card-description {
  padding: 1.5rem;
}
.cards-container .card-description h4 {
  margin: 1rem auto;
}

.task-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}
.task-list a {
  width: 100%;
  margin: 2rem 0;
  display: block;
  margin: 1.5rem auto;
}
.task-list a:hover {
  color: #244a62;
  font-weight: bold;
}
@media (min-width: 768px) {
  .task-list a {
    width: calc(50% - 1rem);
  }
}

* {
  font-family: "DotGothic16", sans-serif;
}

#body {
  /* Encriptador - 1 Desktop */
  position: relative;
  width: 1440px;
  height: 1024px;
  /* Light blue/light-blue-100 */
  background: #F3F5FC;
}

.logo {
  /* Auto layout */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px;
  gap: 40px;
  position: absolute;
  width: 120px;
  height: 48px;
  left: 40px;
  top: 40px;
}

.bar-container {
  background-color: #cccccc;
  padding: 2rem;
}
.bar-container em strong {
  color: red;
}

.bar-diferenciales {
  background-color: #fff;
  padding: 2rem;
}
.bar-diferenciales ul {
  text-align: start;
  display: inline-block;
  vertical-align: top;
  width: 20%;
  margin-right: 15%;
}
.bar-diferenciales ul li {
  font-style: italic;
}
.bar-diferenciales img {
  width: 50%;
  display: inline-block;
}

.principal-title {
  padding-left: 3.8rem;
  margin-top: 2rem;
}

.barber-img img {
  width: auto;
  margin: 0 auto;
}

.barber-header {
  box-sizing: border-box;
  background-color: #bbb;
  padding: 2rem 0;
}
.barber-header .header-conteiner {
  max-width: 80%;
  position: relative;
  margin: 0 auto;
}
.barber-header img {
  width: auto;
}
.barber-header nav {
  position: absolute;
  top: 10rem;
  right: 0;
}
.barber-header nav li {
  display: inline-block;
}
.barber-header nav a {
  text-transform: uppercase;
  color: #000;
  font-weight: bold;
  font-size: 2.2rem;
  margin: 0 0 0 1.5rem;
}
.barber-header nav a:hover {
  color: orange;
  text-decoration: underline;
}

.barber-products {
  max-width: 80%;
  margin: 0 auto;
  padding: 5rem;
  box-sizing: border-box;
}
.barber-products li {
  display: inline-block;
  width: 30%;
  vertical-align: top;
  margin: 0 1.5%;
  padding: 3rem 2rem;
  box-sizing: border-box;
  border: 0.5rem solid #050505;
  border-radius: 2rem;
}
.barber-products li:hover {
  border-color: orange;
  cursor: pointer;
}
.barber-products li:hover h2 {
  font-size: 3.3rem;
}
.barber-products h2 {
  font-size: 3rem;
  font-weight: bold;
  margin: 0;
}
.barber-products .prod-description {
  font-size: 1.8rem;
}
.barber-products .prod-price {
  font-weight: bold;
  font-size: 2rem;
  margin-top: 1rem;
}

.barber-footer {
  text-align: center;
}
.barber-footer p {
  color: white;
  font-size: 1.3rem;
  padding: 4rem 0;
}

.contact-form {
  max-width: 94rem;
  margin: 0 auto;
}
.contact-form form {
  margin: 4rem auto;
}
.contact-form label,
.contact-form input,
.contact-form textarea,
.contact-form p {
  display: block;
  margin: 0 1rem;
  padding: 1rem;
  width: 50%;
  margin: 0.5rem auto;
}
.contact-form textarea {
  resize: none;
}